/**  Custom Formatter */
// format de l'adresse de la machine
function LocaliteFormatter(cell, row) {
    const {lieu, pays, quartier, ville} = new Object(row.localite)
    return (
        <address>
            <span> {quartier} </span>
            <br/>
            <small className="text-italic"> {lieu} </small>
        </address>
    )
}

// format de la date
function DateFormatter(cell, row) {
    const date = new Date(row.createdAt)
    const created = date.toUTCString()
    
    return(
        <span> {created} </span> 
    )
}

// format du status de la machine
function StatusFormatter(cell, row) {
    if (row.status) {
      return (<div className="circle circle-lg bg-success" data-title="normal"/>);
    }

    return (<div className="circle circle-lg bg-danger" data-title="normal"/>);
}

// format du niveau de remplissage de la machine
function LevelFormatter(cell, row) {
    let vari;
    row.level == 0 ? (
        vari = (<Progress color='gray-light' value='100'> 0% </Progress>)
    ): row.level < 50? (
        vari = (<Progress color='warning' value={cell}> {cell}% </Progress>)        
    ) : row.level < 75? (
        vari = (<Progress color='warning' value={cell}> {cell}% </Progress>)        
    ) : ( 
        vari = (<Progress color='danger' value={cell}> {cell}% </Progress>)        
    )
    return vari;
}

// format des options sur les données
function OptionFormatter(cell, row, rowIndex) {
    return (
        <div className="md-6 row ">
            <div className='pl-3 pr-3'>
                <Link to={`/show_machine/${row.key}`}><span className="fas fa-info-circle fa-1x text-info"/> </Link>
            </div>
            <div className='pl-3'>
                <Link to={`/edit_machine/${row.key}`}><span className="fas fa-pencil-alt fa-1x text-warning"/> </Link>
            </div>
        </div>
    )
}

